import { Component, OnInit } from '@angular/core';
import { TranslateService } from './../../../lang-def/translate.service';
import { ActivatedRoute } from '@angular/router';
import { GetFileService, LetterInterface } from './../../../services/file-services/get-file.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { DocDisplayPopupComponent } from './../../doc-display/doc-display-popup.component';

@Component({
  selector: 'app-letters',
  templateUrl: './letters.component.html',
  styleUrls: ['./letters.component.css']
})
export class LettersComponent implements OnInit {
  dialogRef: any;
  user: FormGroup;
  letters: LetterInterface[];
  displayedColumns:string[] = ['title','date','link'];

  private _lang = 'zh';
  constructor(private route: ActivatedRoute, public dialog:MatDialog, private getFile:GetFileService, private translate: TranslateService) {
    this.letters = getFile.getLetters();
  }

  ngOnInit(): void {
    this.subscribeToLangChanged();
    this._lang = this.translate.currentLang;
  }
  subscribeToLangChanged() {
    return this.translate.onLangChanged.subscribe(
      x => this.refreshText(x)
    );
  }
  refreshText(langx) {
    this._lang = langx;
  }

  get showch () {
    return this._lang === 'zh';
  }

  openDoc(data, title) {
    console.log('lang='+ this.translate.currentLang);
    this.dialogRef = this.dialog.open(DocDisplayPopupComponent, {
      width: '100%',
      data: { title: title, filename: data }
    });

    this.dialogRef.afterClosed().subscribe(res => {
    });

  }

}
