import { EfcieStaticModule } from './efcie-static/efcie-static.module';
import { SafePipe } from './../safe.pipe';
import { LangDefModule } from './../lang-def/lang-def.module';
import { NgModule, CUSTOM_ELEMENTS_SCHEMA } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { FormModuleComponent } from './form-module.component';
import { MatTableModule } from '@angular/material/table';
import { NavRoutingModule } from '../nav-module/nav-module-routing.module';
import { DocDisplayComponent } from './doc-display/doc-display.component';
import { DocDisplayPopupComponent } from './doc-display/doc-display-popup.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';

@NgModule({
  imports: [
    BrowserAnimationsModule, CommonModule, FormsModule, ReactiveFormsModule, LangDefModule, MatTableModule, NavRoutingModule,
    EfcieStaticModule
  ],
  declarations: [FormModuleComponent, DocDisplayComponent, SafePipe, DocDisplayPopupComponent]
  ,exports:     [MatTableModule, DocDisplayComponent]
  ,entryComponents: [DocDisplayPopupComponent]
  ,schemas: [CUSTOM_ELEMENTS_SCHEMA]
})
export class FormModule { }
