import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

@Component({
  selector: 'app-doc-display-popup',
  templateUrl: './doc-display-popup.component.html',
  styleUrls: ['./doc-display-popup.component.css']
})
export class DocDisplayPopupComponent implements OnInit {

  constructor(public dialogRef: MatDialogRef<DocDisplayPopupComponent>, @Inject(MAT_DIALOG_DATA) public data: any) { }

  ngOnInit(): void {
  }

  onCloseClick(): void {
    this.dialogRef.close();
  }
}
