import { Pipe, PipeTransform } from '@angular/core';
import { TranslateService, TTranslateServiceGender } from './translate.service';

@Pipe({
  name: 'translate',
  pure: false
})
export class TranslatePipe implements PipeTransform {

    constructor(private _translate: TranslateService) { }
    
    transform(key: string, words?: { [name: string]: string }, gender?: TTranslateServiceGender, count?: number): any {
        if (!key) return;
        return this._translate.instant(key, words, gender, count);
    }    

}
