import { GetFileService } from './../../services/file-services/get-file.service';
import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common'
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { DocDisplayPopupComponent } from './doc-display-popup.component';

@Component({
  selector: 'app-doc-display',
  template: '<div></div>',
  styleUrls: ['./doc-display.component.css']
})
export class DocDisplayComponent implements OnInit {

  filename: string;
  title: string = 'Article Title';
  dialogRef:any;

  constructor(public dialog: MatDialog, private location:Location, private fileService:GetFileService) { }

  ngOnInit(): void {
    const _state: any = this.location.getState();
    let state = '';
    try {
      let k:any;
      for (k in _state) {
        if(k!=='navigationId') state += _state[k];
      };
      this.filename = state;
      this.openDialog();
    } catch (e) {
      console.log(e);
    }

  }

  openDialog(): void {
    this.dialogRef = this.dialog.open(DocDisplayPopupComponent, {
      width: '100%',
      data: { title: this.title, filename: this.filename }
    });

    this.dialogRef.afterClosed().subscribe(res => {
    });
  }
}
