import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs'
import * as data from '../../../assets/json/letterFiles.json';
import * as dataEn from '../../../assets/json/letterFiles_en.json';
import * as rptData2019 from '../../../assets/json/reports2019.json';
import * as rptData2019En from '../../../assets/json/reports2019_en.json';
import * as rptData2020 from '../../../assets/json/reports2020.json';
import * as rptData2020En from '../../../assets/json/reports2020_en.json';

export interface LetterInterface {
  title: string;
  date: any;
  link: string;
}

@Injectable({
  providedIn: 'root'
})
export class GetFileService {
  private _fileUrl = 'assets/letterFiles.json';
  letters: LetterInterface[] = (data as any).default;
  lettersEn: LetterInterface[] = (dataEn as any).default;
  rptData2019: LetterInterface[] = (rptData2019 as any).default;
  rptData2019En: LetterInterface[] = (rptData2019En as any).default;
  rptData2020: LetterInterface[] = (rptData2020 as any).default;
  rptData2020En: LetterInterface[] = (rptData2020En as any).default;

  constructor(private http: HttpClient) {
    //console.log(data);

    // this.getJson().subscribe(data => {
    //   console.log(data);
    // })
  }

  // read a file from server
  public getJson(url): Observable<any> {
    return this.http.get(url||this._fileUrl);
  }
  public getLetters(): LetterInterface[] {
    return this.letters;
  }
}
