import { TranslateService } from './../../../lang-def/translate.service';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { MatCarousel, MatCarouselComponent } from '@ngmodule/material-carousel';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  private _lang = 'zh';
  constructor(private translate: TranslateService, private route: ActivatedRoute) { }

  ngOnInit(): void {
    this.subscribeToLangChanged();
    //this._lang = this.route.snapshot.params.lang||'zh';
    this._lang = this.translate.currentLang;
  }

  subscribeToLangChanged() {
    return this.translate.onLangChanged.subscribe(
      x => this.refreshText(x)
    );
  }
  refreshText(langx) {
    this._lang = langx;
  }

  get showch () {
    return this._lang === 'zh';
  }

  public images = [
    {image: 'assets/img/slideshow/efc-27th.jpg'},
    {image: 'assets/img/slideshow/choir.jpg'},
    {image: 'assets/img/slideshow/dinner.jpg'},
    {image: 'assets/img/slideshow/easter2019.jpg'},
    {image: 'assets/img/slideshow/party.jpg'}
  ];
}
