import { Injectable, Inject, EventEmitter, LOCALE_ID } from '@angular/core';
import { I18nPluralPipe } from '@angular/common'

import { TRANSLATIONS } from './translations';

import { TranslateInvalidArgumentException } from './translate.exception';

export type TTranslateServiceGender = 'male' | 'female' | 'other';


@Injectable()
export class TranslateService {
    private _currentLang: string;
    private PLACEHOLDER = '%';
    private _defaultLang: string;
    private _fallback: boolean;

    public onLangChanged: EventEmitter<string> = new EventEmitter<string>();

    public get currentLang() {
        return this._currentLang || this._defaultLang;
    }

    public setDefaultLang(lang: string) {
        this._defaultLang = lang;
    }

    public enableFallback(enable: boolean) {
        this._fallback = enable;
    }

    constructor(@Inject(TRANSLATIONS) private _translations:any, 
        @Inject(LOCALE_ID) private _locale_id:string, 
        private _pluralPipe: I18nPluralPipe
    ) {
        console.log("LOCALE_ID=" + _locale_id);
        _locale_id = "zh-cn";
        if (typeof this._translations !== 'object' || this._translations === null) {
            throw new TranslateInvalidArgumentException(_translations);
        }
        
        // make deep copy of translations
        this._translations = JSON.parse(JSON.stringify(this._translations));

        // transform if not
        if (!this._translations.alreadyCompiledByTranslateService) {
            let error = false;
            // Validate / normalize structure
            for (var langage in this._translations) {
                var langDic: { [token: string]: any } = this._translations[langage];
                for (var token in langDic) {
                    var translation: any = langDic[token];

                    // is string ?
                    if (typeof translation === 'string') {
                        translation = { 'other': { 'other': translation } };
                    } else if (typeof translation === 'object') {
                        var level: 'sex' | 'count' = 'sex'; // by default use sex

                        for (var key in translation) {
                            if (/^=[0-9+]$/.test(key)) {
                                level = 'count';
                            }
                        }

                        if (level === 'sex') {
                            if (typeof translation.male === 'string') {
                                translation.male = { 'other': translation.male };
                            }
                            if (typeof translation.female === 'string') {
                                translation.female = { 'other': translation.female };
                            }
                            if (typeof translation.other === 'string') {
                                translation.other = { 'other': translation.other };
                            }
                        } else { // count
                            translation = { 'other': translation };
                        }
                    } else {
                        error = true;
                    }

                    if (error) {
                        throw new TranslateInvalidArgumentException(langDic[token], `in ${langage} key ${token}`);
                    }
                    langDic[token] = translation;
                }
            }

            if (!error) {
                this._translations.alreadyCompiledByTranslateService = true;
            }
        }    
    }

    public use(lang: string): void {
        // set current language
        this._currentLang = lang;
        this.onLangChanged.emit(lang);
    }
    
    public replace(word: string = '', words: string | string[] = '') {
        let translation: string = word;

        const values: string[] = [].concat(words);
        values.forEach((e, i) => {
            translation = translation.replace(this.PLACEHOLDER.concat(<any>i), e);
        });

        return translation;
    }

    public instant(key: string, words?: { [name:string] : string}, gender?: TTranslateServiceGender, count?: number) {
        // public perform translation
        return this.translate(key, words, gender, count);
    }
    
    private translate(key: string, 
        replaceValues?: { [name: string]: string }, 
        gender?: TTranslateServiceGender, 
        count?: number): string {
        
        var translation = (this._translations[this.currentLang] && this._translations[this.currentLang][key])
            || (this._fallback && this._translations[this._defaultLang] && this._translations[this._defaultLang][key]);

        var value = '';
        if (translation) {
            var genderTranslation = translation[gender] || translation['other'] || {};

            value = this._pluralPipe.transform(count, genderTranslation) || genderTranslation['other'] || '';

            // replace var if any
            var matches = value.match(/\$\{.*?}/g);   // search for ${exp}
            if (matches) { // contains var
                for (var i = 0; i < matches.length; ++i) {
                    var replacedValue: string = '(undefined)';
                    var exp = matches[i].replace(' ', '').match(/[^${}]+/); // remove spaces
                    if (exp) {
                        var memberName: string = exp[0];

                        if (replaceValues) {
                            replacedValue = replaceValues[memberName];
                        }
                    }

                    if (replacedValue === undefined) {
                        replacedValue = '(undefined)';
                    }

                    if (replacedValue === null) {
                        replacedValue = '(null)';
                    }

                    value = value.replace(matches[i], replacedValue);
                }
            }
        } else {
            console.log(`No translation for ${key} for local ${this.currentLang} and ${this._defaultLang} (or fallback not configured)`);
        }

        // not found
        return value;
    }    
}
