import { Type } from '@angular/core';

// From https://github.com/angular/angular/blob/2.1.1/modules/%40angular/facade/src/errors.ts

export class BaseException extends Error {
    _nativeError: Error;

    constructor(message: string) {
        const nativeError = super(message) as any as Error;
        this._nativeError = nativeError;
    }

    get message() { return this._nativeError.message; }
    set message(message) { this._nativeError.message = message; }
    get name() { return this._nativeError.name; }
    get stack() { return (this._nativeError as any).stack; }
    set stack(value) { (this._nativeError as any).stack = value; }
    toString() { return this._nativeError.toString(); }
}

export class TranslateInvalidArgumentException extends BaseException {
   
    constructor(value: Object, complement?: string) {
        super(`Invalid argument '${value}' for service Translate`);

        if (!complement) {
            this.message = `Invalid argument '${value}' for service Translate. Complement : ${complement}.`;
        }
    }
}