import { TranslateService } from './../../lang-def/translate.service';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-z-mat-menu',
  templateUrl: './z-mat-menu.component.html',
  styleUrls: ['./z-mat-menu.component.css']
})
export class ZMatMenuComponent implements OnInit {
  public supportedLangs: any[];
  public lang = 'zh';
  constructor(private _translate: TranslateService) {
  }

  ngOnInit(): void {
    this.supportedLangs = [
      { display: '华', value: 'zh' },
      { display: 'En', value: 'en' }
    ];
    this._translate.setDefaultLang('zh');
    this._translate.enableFallback(true);
    this.selectLang('zh');   //es,en 
  }
  isCurrentLang(lang: string) {
    return lang === this._translate.currentLang;
  }

  selectLang(lang: string) {
    console.log('selected lang='+lang);
    this._translate.use(lang);
    this.subscribeToLangChanged();
  }
  subscribeToLangChanged() {
    return this._translate.onLangChanged.subscribe(x => {this.lang = x;  console.log(x)});
  }

  toggle(): void {
    console.log("toggled ");
  }

}
